import React from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

const texto = `El Proyecto Canelón nace como iniciativa de la Comisión Fomento de Shangrilá,
sumándose vecinas y vecinos con formación académica en el tema.`;

const texto_2 = `Al ser presentado al Gobierno Departamental y Municipal, ellos abalan nuestra iniciativa 
y se incorporan al equipo de trabajo.`;

const texto_3 = `También se suman con un apoyo económico la Fundación Verde y Rotunda y con un apoyo técnico Plantaton Uruguay, 
la Red de Huertas Uruguay y proyecto municipal Manos a la Huerta. `;

const texto_4 = `Con el objetivo de tener una ciudad más verde, apoyando la conservación de nuestro ecosistema y contribuyendo 
a la disminución del impacto ambiental, impulsamos la plantación de árboles nativos en Ciudad de la Costa.`;

function Intro() {
  return (
    <Container id="intro" className="cuadro mt-4">
    
        <h2 className="titulo row display-3 justify-content-center"> Proyecto Canelón</h2>
      
      <Row>
        <Col>
          {" "}
          <h4 className="texto fw-light"> {texto} </h4>{" "}
        </Col>
      </Row>
      <Row>
        <Col>
          {" "}
          <h4 className="texto fw-light mt-2"> {texto_2} </h4>{" "}
        </Col>
      </Row>
   <Row>
        <Col>
          {" "}
          <h4 className="texto fw-light mt-2"> {texto_3} </h4>{" "}
        </Col>
      </Row> 
      <Row>
        <Col>
          {" "}
          <h4 className="texto fw-light mt-2"> {texto_4} </h4>{" "}
        </Col>
      </Row>
      </Container>
  );
}

export default Intro;
